/**************ballon.min.css*******************************/

:root {
  --balloon-color: rgba(16, 16, 16, 0.95);
  --balloon-font-size: 12px;
  --balloon-move: 4px;
}

button[data-balloon] {
  overflow: visible;
}

[data-balloon] {
  position: relative;
  cursor: pointer;
}

[data-balloon]:after {
  opacity: 0;
  pointer-events: none;
  transition: all 0.18s ease-out 0.18s;
  text-indent: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-shadow: none;
  font-size: var(--balloon-font-size);
  background: var(--balloon-color);
  border-radius: 2px;
  color: #fff;
  content: attr(data-balloon);
  padding: 0.5em 1em;
  position: absolute;
  white-space: nowrap;
  z-index: 10;
}

[data-balloon]:before {
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-top-color: var(--balloon-color);
  opacity: 0;
  pointer-events: none;
  transition: all 0.18s ease-out 0.18s;
  content: "";
  position: absolute;
  z-index: 10;
}

[data-balloon]:hover:after,
[data-balloon]:hover:before,
[data-balloon]:not([data-balloon-nofocus]):focus:after,
[data-balloon]:not([data-balloon-nofocus]):focus:before,
[data-balloon][data-balloon-visible]:after,
[data-balloon][data-balloon-visible]:before {
  opacity: 1;
  pointer-events: none;
}

[data-balloon]:not([data-balloon-pos]):after {
  bottom: 100%;
  left: 50%;
  margin-bottom: 10px;
  transform: translate(-50%, var(--balloon-move));
  transform-origin: top;
}

[data-balloon]:not([data-balloon-pos]):before {
  bottom: 100%;
  left: 50%;
  transform: translate(-50%, var(--balloon-move));
  transform-origin: top;
}

[data-balloon]:not([data-balloon-pos]):hover:after,
[data-balloon]:not([data-balloon-pos])[data-balloon-visible]:after {
  transform: translate(-50%, 0);
}

[data-balloon]:not([data-balloon-pos]):hover:before,
[data-balloon]:not([data-balloon-pos])[data-balloon-visible]:before {
  transform: translate(-50%, 0);
}

[data-balloon].font-awesome:after {
  font-family: FontAwesome, -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

[data-balloon][data-balloon-break]:after {
  white-space: pre;
}

[data-balloon][data-balloon-break][data-balloon-length]:after {
  white-space: pre-line;
  word-break: break-word;
}

[data-balloon][data-balloon-blunt]:after,
[data-balloon][data-balloon-blunt]:before {
  transition: none;
}

[data-balloon][data-balloon-pos="up"]:after {
  bottom: 100%;
  left: 50%;
  margin-bottom: 10px;
  transform: translate(-50%, var(--balloon-move));
  transform-origin: top;
}

[data-balloon][data-balloon-pos="up"]:before {
  bottom: 100%;
  left: 50%;
  transform: translate(-50%, var(--balloon-move));
  transform-origin: top;
}

[data-balloon][data-balloon-pos="up"]:hover:after,
[data-balloon][data-balloon-pos="up"][data-balloon-visible]:after {
  transform: translate(-50%, 0);
}

[data-balloon][data-balloon-pos="up"]:hover:before,
[data-balloon][data-balloon-pos="up"][data-balloon-visible]:before {
  transform: translate(-50%, 0);
}

[data-balloon][data-balloon-pos="up-left"]:after {
  bottom: 100%;
  left: 0;
  margin-bottom: 10px;
  transform: translate(0, var(--balloon-move));
  transform-origin: top;
}

[data-balloon][data-balloon-pos="up-left"]:before {
  bottom: 100%;
  left: 5px;
  transform: translate(0, var(--balloon-move));
  transform-origin: top;
}

[data-balloon][data-balloon-pos="up-left"]:hover:after,
[data-balloon][data-balloon-pos="up-left"][data-balloon-visible]:after {
  transform: translate(0, 0);
}

[data-balloon][data-balloon-pos="up-left"]:hover:before,
[data-balloon][data-balloon-pos="up-left"][data-balloon-visible]:before {
  transform: translate(0, 0);
}

[data-balloon][data-balloon-pos="up-right"]:after {
  bottom: 100%;
  right: 0;
  margin-bottom: 10px;
  transform: translate(0, var(--balloon-move));
  transform-origin: top;
}

[data-balloon][data-balloon-pos="up-right"]:before {
  bottom: 100%;
  right: 5px;
  transform: translate(0, var(--balloon-move));
  transform-origin: top;
}

[data-balloon][data-balloon-pos="up-right"]:hover:after,
[data-balloon][data-balloon-pos="up-right"][data-balloon-visible]:after {
  transform: translate(0, 0);
}

[data-balloon][data-balloon-pos="up-right"]:hover:before,
[data-balloon][data-balloon-pos="up-right"][data-balloon-visible]:before {
  transform: translate(0, 0);
}

[data-balloon][data-balloon-pos="down"]:after {
  left: 50%;
  margin-top: 10px;
  top: 100%;
  transform: translate(-50%, calc(var(--balloon-move) * -1));
}

[data-balloon][data-balloon-pos="down"]:before {
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-bottom-color: var(--balloon-color);
  left: 50%;
  top: 100%;
  transform: translate(-50%, calc(var(--balloon-move) * -1));
}

[data-balloon][data-balloon-pos="down"]:hover:after,
[data-balloon][data-balloon-pos="down"][data-balloon-visible]:after {
  transform: translate(-50%, 0);
}

[data-balloon][data-balloon-pos="down"]:hover:before,
[data-balloon][data-balloon-pos="down"][data-balloon-visible]:before {
  transform: translate(-50%, 0);
}

[data-balloon][data-balloon-pos="down-left"]:after {
  left: 0;
  margin-top: 10px;
  top: 100%;
  transform: translate(0, calc(var(--balloon-move) * -1));
}

[data-balloon][data-balloon-pos="down-left"]:before {
  width: 0;
  height: 0;
  border: 5px solid transparent;
  border-bottom-color: var(--balloon-color);
  left: 5px;
  top: 100%;
  transform: translate(0, calc(var(--balloon-move) * -1));
}

[data-balloon][data-balloon-pos="down-left"]:hover:after,
[data-balloon][data-balloon-pos="down-left"][data-balloon-visible]:after {
  transform: translate(0, 0);
