.floting-bar-wrap {
  position: fixed;
  top: 0;
  right: -400px;
  z-index: 1000;
  width: 450px;
  height: 100%;
  display: inline-table;
  opacity: 0;
  padding: 120px 65px;
  -webkit-transform: translateX(101%);
  -moz-transform: translateX(101%);
  -ms-transform: translateX(101%);
  -o-transform: translateX(101%);
  transform: translateX(101%);
  transition: all 900ms ease;
  -moz-transition: all 900ms ease;
  -webkit-transition: all 900ms ease;
  -ms-transition: all 900ms ease;
  -o-transition: all 900ms ease;
  -webkit-transition-delay: 300ms;
  -moz-transition-delay: 300ms;
  -ms-transition-delay: 300ms;
  -o-transition-delay: 300ms;
  transition-delay: 300ms;
  background-color: var(--pbmit-xinterio-light-bg-color);
}

body.rtl .floting-bar-wrap {
  left: -400px;
  right: inherit;
  -webkit-transform: translateX(-101%);
  -moz-transform: translateX(-101%);
  -ms-transform: translateX(-101%);
  -o-transform: translateX(-101%);
  transform: translateX(-101%);
}

body.rtl .floting-bar-wrap.active,
.floting-bar-wrap.active {
  right: 0;
  visibility: visible;
  opacity: 1;
  overflow-y: auto;
  transition: all 900ms ease;
  -moz-transition: all 900ms ease;
  -webkit-transition: all 900ms ease;
  -ms-transition: all 900ms ease;
  -o-transition: all 900ms ease;
  -webkit-transform: translateX(0);
  -moz-transform: translateX(0);
  -ms-transform: translateX(0);
  -o-transform: translateX(0);
  transform: translateX(0);
}

body.rtl .floting-bar-wrap.active {
  left: 0;
  right: inherit;
}

.admin-bar .floting-bar-wrap .closepanel {
  margin-top: 32px;
}

.floting-bar-wrap .closepanel {
  position: absolute;
  z-index: 600;
  right: 0;
  top: 40px;
  display: block;
  width: 80px;
  height: 80px;
  font-size: 30px;
  line-height: 75px;
  text-align: center;
  cursor: pointer;
  color: var(--pbmit-xinterio-blackish-color);
  background-color: var(--pbmit-xinterio-global-color);
  -webkit-transition: all 300ms ease;
  -moz-transition: all 300ms ease;
  -ms-transition: all 300ms ease;
  -o-transition: all 300ms ease;
  transition: all 300ms ease;
  -webkit-transform: translate(0%, -50%) rotate(0deg);
  -moz-transform: translate(0%, -50%) rotate(0deg);
  -ms-transform: translate(0%, -50%) rotate(0deg);
  -o-transform: translate(0%, -50%) rotate(0deg);
  transform: translate(0%, -50%) rotate(0deg);
}

@keyframes pbmit-closer-line-draw {
  0%,
  100% {
    clip-path: inset(-1px 0 -1px 0);
    -webkit-clip-path: inset(-1px 0 -1px 0);
  }

  55% {
    clip-path: inset(-1px 0 -1px 100%);
    -webkit-clip-path: inset(-1px 0 -1px 100%);
  }

  56% {
    clip-path: inset(-1px 100% -1px 0);
    -webkit-clip-path: inset(-1px 100% -1px 0);
  }
}

.floting-bar-wrap .closepanel:hover svg rect {
  animation: pbmit-closer-line-draw 0.6s;
  animation-duration: 0.6s;
  animation-timing-function: ease;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
  animation-name: pbmit-closer-line-draw;
}

.floting-bar-wrap .closepanel:hover svg rect:nth-of-type(2) {
  animation-delay: 0.17s;
}

.floting-bar-wrap .closepanel svg rect {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

